
import XCircle from 'vux/src/components/x-circle/index.vue'
import Search from 'vux/src/components/search/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import Scroll from './pulldown'

const imgList = [
'https://static.vux.li/demo/1.jpg',
'https://static.vux.li/demo/2.jpg',
'https://static.vux.li/demo/3.jpg'
]

export default {
components: {
XCircle,
Search,
LoadMore,
Swiper,
SwiperItem,
Cell,
Group,
Badge,
Divider,
Tab,
TabItem,
'v-scroll': Scroll
},
data () {
return {
demo04_list: imgList,
strokeWidth: 15,
trailWidth: 15,
index: 0,
cellTitle: '',
loadmore: false,
loadbottom: false,
carList: [],
loadingText: '',
pageNum: 1, // 当前页面
pageSize: 30,  // 一次显示多少条
downdata: []  // 上拉更多的数据存放数组
}
},
created: function () {
this.fetchData(this.pageNum, this.pageSize)
},
watch: {

'$route': 'fetchData'
},
methods: {
fetchData: function (pageNum, pageSize) {
this.loadmore = true
var params = 'pageNum=' + pageNum + '&pageSize=' + pageSize
this.$http.post(this.domain + '/api/anon/car/getCarList', params).then((response) => {
this.carList = response.data.data
this.loadmore = false
})
},
onRefresh (done) {

this.loadbottom = false
this.carList = []
this.pageNum = 1
this.fetchData(1, 30)
done()
},
onInfinite (done) {
if (this.loadbottom === true) {
done()
return
} else {
this.loadmore = true
this.pageNum++
let pageNum = this.pageNum
var grade = this.getGrade(this.index)
var params = 'pageNum=' + pageNum + '&pageSize=' + this.pageSize + '&grade=' + grade
this.$http.post(this.domain + '/api/anon/activity/getActivityList', params).then((response) => {

this.downdata = response.data.data
this.carList = this.carList.concat(this.downdata)
if (this.downdata.length === 0) {
this.loadbottom = true
}
this.loadmore = false
done()
})
}
}
}
}



